.logo-card {
    padding: 34px 48px 35px 30px;
    border: 1px solid #CDCDCD;
    border-radius: 10px;
    @include transition;
    margin-bottom: 11px;
    height: 150px;
    background: $color-white;
    
    .logo {
        img {
            margin-bottom: 8px;
        }
        .text {
            font-family: $font-roboto;
            font-weight: 400;
            @include toRem(font-size, 20);
            line-height: 28px;
            color: #8E8E8F;
        }
    }
    
    &:hover {
        box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.08);
        .logo-card-btn {
            svg {
                fill: #1D2746;
                @include transition;
            }
        }
    }
    &:last-child {
        margin-bottom: 0;
    }
    &--style-1 {
        
    }
    &--style-2 {
        box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.08);
        border: 1px solid transparent;
        @include transition;
        &:hover {
            border: 1px solid $color-shade24;
            box-shadow: none;
            .logo-card-btn {
                svg {
                    fill: $color-shade24;
                    @include transition;
                }
            }
        }
    }
    &--style-3 {
        box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.05);
        border: 1px solid transparent;
        padding: 35px 56px;
        height: unset;
        margin-bottom: 20px;
        .logo {
            margin-bottom: 30px;
            img {
                margin-bottom: 20px;
            }
        }
        .post-info-wrapper {
            column-gap: 63px;
            row-gap: 25px;
        }
        @include transition;
        &:hover {
            border: 1px solid $color-shade25;
            box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.05);
        }
    }
}